// bibliotek
import React from "react";
import Marquee from "react-fast-marquee";
// style
import "./footer.scss"



// images
import footer_bg from "../../assets/footer/1.png"
import socialIcon_1 from "../../assets/socialKastom/whatsapp.svg"
import socialIcon_2 from "../../assets/socialKastom/instagram.svg"
import socialIcon_3 from "../../assets/socialKastom/telegram.svg"
import socialIcon_4 from "../../assets/socialKastom/gmail.svg"
import socialIcon_5 from "../../assets/icon/instagram.svg"
import socialIcon_6 from "../../assets/icon/telegram.svg"
import socialIcon_7 from "../../assets/icon/whatsapp.svg"


function Footer(){
    return(
        <div className="footer">
            {/* <Paragraph title="contacts"/> */}
            <div className="footer__wrapper">
                <div className="footer__container">
                    <div className="footer__logo">
                        <Marquee speed={90}>
                            <h4>contacts</h4>
                            <h5>g</h5>
                            <h4>contacts</h4>
                            <h5>g</h5>
                            {/* <h4>q2-studia</h4> 
                            <h4>-design-</h4> 
                            <h4>beauty-</h4> */}
                        </Marquee>
                    </div>
                    <div className="footer__contact">
                    <form>
                        <div class="segment">
                            <h2>Application</h2>
                        </div>
                        <label>
                            <input type="text" placeholder="Name"/>
                        </label>
                        <label>
                            <input type="text" placeholder="Email Address"/>
                        </label>
                        
                        <button class="red" type="button">Send</button>
                    </form>
                    <div className="footer__connect">
                        <ul>
                            <div className="footer__connect__left">
                            <li className="footer__connect__item"><img src={socialIcon_1} alt="" /><a href="#">WhatsApp<span> </span></a></li>
                            <li className="footer__connect__item"><img src={socialIcon_2} alt="" /><a href="#">Telegram<span> Tik tok</span></a></li>
                            <li className="footer__connect__item"><img src={socialIcon_3} alt="" /><a href="#">Pinterest<span>  </span></a></li>
                            </div>
                            <div className="footer__connect__right">
                            <li className="footer__connect__item"><img src={socialIcon_7} alt="" /><a href="#">WhatsApp<span>WhatsApp</span></a></li>
                            <li className="footer__connect__item"><img src={socialIcon_6} alt="" /><a href="#">Email<span> </span></a></li>
                            <li className="footer__connect__item"><img src={socialIcon_5} alt="" /><a href="#">Phone<span>100% </span></a></li>
                            </div>
                        </ul>
                    </div>
                    </div>
                </div>
                <div className="footer__images">
                    <img src={footer_bg} alt="" />
                </div>
            </div>
            
        </div>
    )
}

export default Footer;