// bibliotek
import React, { useState } from "react";
import { Link } from "react-router-dom";

import MagneticSocial from "../magneticSocial/MagneticSocial.jsx";

// style
import './header.scss';
// images
import homeHeader from "../../assets/header/home.svg";
import telegram from "../../assets/header/telegram.svg"
import whatsapp from "../../assets/header/whatsapp.svg"
import linkedin from "../../assets/header/linkedin.svg"


function Header() {
  
  const [isShown, setIsShown] = useState(false);

  const handleClick = event => {
    setIsShown(current => !current);
  };
  
    return (
      <>
      <HeaderNav shown={isShown} setshown={setIsShown}/>
      <header className="header">
        <Link to="/"><h2 className="logo">Q2<span>Studio</span></h2></Link>
        
        <button className="burger" onClick={handleClick}>
          {isShown && <HeaderNav />}
            <span className='burger__item_1'></span>
            <span className='burger__item_2'></span>
            <span className='burger__item_3'></span>
           
        </button>
        <div className="header__social">
          <MagneticSocial>
          <img  src={telegram} alt="" />
          </MagneticSocial>
          <MagneticSocial>
          <img  src={whatsapp} alt="" />
          </MagneticSocial>
          <MagneticSocial>
          <img  src={linkedin} alt="" />
          </MagneticSocial>
        </div>
      </header>
      </>
    );
  }
  
  export default Header;


  function HeaderNav({shown}){
    return(
      <nav className={shown ? "nav__wrapper active" : "nav__wrapper"}>
          <ul >
            <li><Link className='nav_item' to="/"><img src={homeHeader} alt="" /><h5>Home</h5></Link></li>
            <li><Link to="/picture">Gallery</Link></li>
            <li><a href="#">About</a></li>
            <li><a href="#">Team</a></li>
            <li><a href="#">Contact</a></li>
          </ul>
      </nav>
    )
  }