// bibliotek
import React from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import {useRef, useEffect} from "react";

//  
// import { gallery__info } from "../../assets/gallery__info";
// style
import "./scrollGorizont.scss";
// avatar_1
import avatar_1 from "../../assets/avaters/avatart_1/  .png";
import anketa from "../../assets/avaters/avatart_1/.png";
import balls from "../../assets/avaters/avatart_1/ .png";
import konfeta_1 from "../../assets/avaters/avatart_1/ 1.png";
import konfeta_2 from "../../assets/avaters/avatart_1/ 2.png";
import krujka_1 from "../../assets/avaters/avatart_1/  1.png";
import krujka_4 from "../../assets/avaters/avatart_1/  .png";
import krujka_6 from "../../assets/avaters/avatart_1/.png";
import krujka_7 from "../../assets/avaters/avatart_1/.png";
// avatar 2
import balss from "../../assets/avaters/avatar_2/ .png";
import plan_1 from "../../assets/avaters/avatar_2/ 1.png";
import plan_2 from "../../assets/avaters/avatar_2/ 2.png";
import word from "../../assets/avaters/avatar_2/ R.png";
import two from "../../assets/avaters/avatar_2/.png";
import tochka from "../../assets/avaters/avatar_2/.png";
// avatar 3
import balss4_1 from "../../assets/avaters/avatar_3/1.png";
import balss4_2 from "../../assets/avaters/avatar_3/2.png";
import balss4_3 from "../../assets/avaters/avatar_3/3.png";
import balss4_4 from "../../assets/avaters/avatar_3/4.png";
import balss4_5 from "../../assets/avaters/avatar_3/5.png";
import steny from "../../assets/avaters/avatar_3/.png";
import divan from "../../assets/avaters/avatar_3/.png";
import kreslo from "../../assets/avaters/avatar_3/.png";
import kruslo from "../../assets/avaters/avatar_3/ 2.png";
import stol from "../../assets/avaters/avatar_3/ 2.png";
import tochca from "../../assets/avaters/avatar_3/.png";
// avatar 4
import truby from "../../assets/avaters/avatar_4/    .png";
import balss5_1 from "../../assets/avaters/avatar_4/ 1.png";
import balss5_2 from "../../assets/avaters/avatar_4/ 2.png";
import balss5_3 from "../../assets/avaters/avatar_4/ 3.png";
import balss5_4 from "../../assets/avaters/avatar_4/ 4.png";
import balss5_5 from "../../assets/avaters/avatar_4/ 5.png";
import rosetka from "../../assets/avaters/avatar_4/.png";
import tochcca from "../../assets/avaters/avatar_4/.png";
import svetilnik_1 from "../../assets/avaters/avatar_4/  1.png";
import svetilnik_2 from "../../assets/avaters/avatar_4/  2.png";
import uslovRosetka_1 from "../../assets/avaters/avatar_4/  .png";
import uslovRosetka_2 from "../../assets/avaters/avatar_4/   2.png";

function ScrollGorizont(){
    const sectionRef = useRef(null);
    const triggerRef = useRef(null);

    gsap.registerPlugin(ScrollTrigger);

    useEffect(()=>{
        const pin = gsap.fromTo(sectionRef.current, {
            translate: 0,
        },{
            translate: "-300vw",
            ease: "none",
            duration:1,
            scrollTrigger: {
                trigger: triggerRef.current,
                start: "top top",
                end: "2000 top",
                scrub: 1,
                pin: true,
            }
        })
        return()=>{
            pin.kill();
        }
    }, [])
    
    return(
        <section className="scrollGorizont__wrapper">
            <div ref={triggerRef}>
                <div ref ={sectionRef} className="scrollGorizont__container">
                    <div className="scrollGorizont__section_1">
                        <div className="scrollGorizont__section_1_text scrollGorizont__text">
                            <h3> I</h3>
                            <h4> </h4>
                            <ul>
                                <li></li>
                                <li></li>
                                <li> </li>
                            </ul>
                        </div>
                        <div className="scrollGorizont__images_avatar_1">
                            <img className="scrollGorizont__avatar_1" src={avatar_1} alt="avatar_1" />
                            <img className="scrollGorizont__anketa" src={anketa} alt="anketa" />
                            <img className="scrollGorizont__balls" src={balls} alt="balls" />
                            <img className="scrollGorizont__konfeta_1" src={konfeta_1} alt="konfeta_1" />
                            <img className="scrollGorizont__konfeta_2" src={konfeta_2} alt="konfeta_2" />
                            <img className="scrollGorizont__krujka_1" src={krujka_1} alt="krujka_1" />
                            <img className="scrollGorizont__krujka_4" src={krujka_4} alt="krujka_4" />
                            <img className="scrollGorizont__krujka_6" src={krujka_6} alt="krujka_6" />
                            <img className="scrollGorizont__krujka_7" src={krujka_7} alt="krujka_7" />
                        </div>
                    </div>
                    <div className="scrollGorizont__section_2">
                        <div className="scrollGorizont__section_2_text scrollGorizont__text">
                            <h3> II</h3>
                            <h4> </h4>
                            <p>3      .       , ,  .</p>
                        </div>
                        <div className="scrollGorizont__images_avatar_2">
                            <img className="scrollGorizont__balss_1" src={balss} alt="balss" />
                            <img className="scrollGorizont__plan_2" src={plan_1} alt="plan_1" />
                            <img className="scrollGorizont__plan_3" src={plan_2} alt="plan_2" />
                            <img className="scrollGorizont__word_4" src={word} alt="word" />
                            <img className="scrollGorizont__two_5" src={two} alt="two"/>
                            <img className="scrollGorizont__tochka_6" src={tochka} alt="tochka" />
                        </div>
                    </div>
                    <div className="scrollGorizont__section_3">
                        <div className="scrollGorizont__section_3_text scrollGorizont__text">
                            <h3> III</h3>
                            <h4> </h4>
                            <p>     3        ,   -,    .         .</p>
                        </div>
                        <div className="scrollGorizont__images_avatar_3">
                            <img className="scrollGorizont__balss4_1_1" src={balss4_1} alt="balss4_1" />
                            <img className="scrollGorizont__balss4_2_2" src={balss4_2} alt="balss4_2" />
                            <img className="scrollGorizont__balss4_3_3" src={balss4_3} alt="balss4_3" />
                            <img className="scrollGorizont__balss4_4_4" src={balss4_4} alt="balss4_4" />
                            <img className="scrollGorizont__balss4_5_5" src={balss4_5} alt="balss4_5" />
                            <img className="scrollGorizont__steny_6" src={steny} alt="steny" />
                            <img className="scrollGorizont__balss4_4_7" src={balss4_4} alt="balss4_4" />
                            <img className="scrollGorizont__divan_8" src={divan} alt="divan" />
                            <img className="scrollGorizont__kreslo_9" src={kreslo} alt="divan" />
                            <img className="scrollGorizont__kruslo_10" src={kruslo} alt="kruslo" />
                            <img className="scrollGorizont__stol_11" src={stol} alt="stol" />
                            <img className="scrollGorizont__tochca_12" src={tochca} alt="tochca" />

                        </div>
                    </div>
                    <div className="scrollGorizont__section_4">
                        <div className="scrollGorizont__section_4_text scrollGorizont__text">
                            <h3> IV</h3>
                            <h4> </h4>
                            <p>3      .       , ,  .</p>
                        </div>
                        <div className="scrollGorizont__images_avatar_4">
                            <img className="scrollGorizont__truby_1" src={truby} alt="truby" />
                            <img className="scrollGorizont__balss5_1_2" src={balss5_1} alt="balss4_1" />
                            <img className="scrollGorizont__balss5_2_3" src={balss5_2} alt="balss5_2" />
                            <img className="scrollGorizont__balss5_3_4" src={balss5_3} alt="balss5_3" />
                            <img className="scrollGorizont__balss5_4_5" src={balss5_4} alt="balss5_4" />
                            <img className="scrollGorizont__balss5_5_6" src={balss5_5} alt="balss5_5" />
                            <img className="scrollGorizont__balss5_6_7" src={balss5_5} alt="balss5_5" />
                            <img className="scrollGorizont__rosetka_8" src={rosetka} alt="rosetka" />
                            <img className="scrollGorizont__tochcca_9" src={tochcca} alt="tochcca" />
                            <img className="scrollGorizont__svetilnik_1_10" src={svetilnik_1} alt="svetilnik_1" />
                            <img className="scrollGorizont__svetilnik_2_11" src={svetilnik_2} alt="svetilnik_2" />
                            <img className="scrollGorizont__uslovRosetka_1_12" src={uslovRosetka_1} alt="uslovRosetka_1" />
                            <img className="scrollGorizont__uslovRosetka_2_13" src={uslovRosetka_2} alt="uslovRosetka_2" />
                            
                        </div>
                    </div>
                    <div className="scrollGorizont__section_5">
                        
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ScrollGorizont;