// bibliotek
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import {useRef, useEffect} from "react";
// images
import bg_logo from '../../assets/bg_main_logo_img/bg_logo_img.mp4'
import sub_bg_1 from "../../assets/bg_main_logo_img/item_10.jpg"
import sub_bg_2 from "../../assets/bg_main_logo_img/item_11.jpg"
// components

// styli
import "./main.scss"

function Main() {
    gsap.registerPlugin(ScrollTrigger);

    const imgRef =useRef(null);
    useEffect(()=>{
        const el = imgRef.current;
        gsap.fromTo(el, {y:0}, {y:"250px", duration:3, scrollTrigger: {
        trigger: el,
        scrub: true,
        start: "top top",
        end: "+=800",
        }})
    })
    return(
        <>
        <div className="main__bg" >
            {/* <img src={bg_logo} alt="" /> */}
            <video src={bg_logo} type='video/mp4' muted="muted" autoplay="autoplay"  loop></video>
            {/* <div className="main__subbg">
                <div className="main__subbg__left"><img src={sub_bg_1} alt="" /></div>
                <div className="main__subbg__right"><img src={sub_bg_2} alt="" /></div>
            </div> */}
        </div>
        
        </>
    )
}

export default Main;