
import "./paragraph.scss";
// bibliotek
import React from "react"
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import {useRef, useEffect} from "react";

gsap.registerPlugin(ScrollTrigger);


export default function Paragraph({title}){
    //   
const paragrafRef =useRef(null);
useEffect(()=>{
    const el = paragrafRef.current;
    gsap.fromTo(el, {x:"80%", opacity:0}, {x: "0%", opacity:1, duration:3, scrollTrigger: {
    trigger: el,
    scrub: 2,
    start: "top bottom",
    end: "top 60%",
    }});
})
    return(
        <div className="paragraph__wrapper" >
            <h6 className="paragraph__title">{title}</h6>
        </div>
    )
}