import "./portfolio.scss"
// bibliotek
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import {useRef, useEffect} from "react";
import { Link } from "react-router-dom";

// images
import portfolio_bg from "../../assets/bg_main_logo_img/bg.png"
// 
import portfolio_item_1 from "../../assets/portfolio//1.jpg"
import portfolio_item_2 from "../../assets/portfolio//2.jpg"
import portfolio_item_3 from "../../assets/portfolio//3.jpg"
import portfolio_item_4 from "../../assets/portfolio//4.jpg"
import portfolio_item_5 from "../../assets/portfolio//5.jpg"
import portfolio_item_6 from "../../assets/portfolio//6.jpg"
import portfolio_item_7 from "../../assets/portfolio//7.jpg"
import portfolio_item_8 from "../../assets/portfolio//8.jpg"
import portfolio_item_9 from "../../assets/portfolio//9.jpg"
import portfolio_item_10 from "../../assets/portfolio//10.jpg"
import buttonImg from "../../assets/portfolio/button/arrow.gif"
// components
// import useLocoScroll from "../../hooks/useLocoScroll";

gsap.registerPlugin(ScrollTrigger);

function Portfolio() {
    
//    ()
    const imgRef =useRef(null);
    useEffect(()=>{
        const el = imgRef.current;
        gsap.fromTo(el, {y:"10%"}, {y: "-100%", duration:3, scrollTrigger: {
        trigger: el,
        scrub: 1,
        start: "top bottom",
        // end: "top 40%",
        end: "+=1200",
        }})
    })
//   
    const portfolioText =useRef(null);
    useEffect(()=>{
        const el = portfolioText.current;
        gsap.fromTo(el, {opacity: 0, scale: 0.5}, { scale:1, opacity: 1, duration:3, scrollTrigger: {
        trigger: el,
        scrub:1,
        start: "top 80%",
        end: "+=400",
        }})
    })
     //  
    const sectionRef_1 = useRef(null);
    const triggerRef_1 = useRef(null);
    useEffect(()=>{
        const pin = gsap.fromTo(sectionRef_1.current, {
            y: "600px",
            
        },{
            y: 0,
            rotate: "0deg",
            ease: "none",
            duration:1,
            scrollTrigger: {
                trigger: triggerRef_1.current,
                start: "top 1%",
                // end: "+=1500",
                end: "bottom 90%",
                scrub: 1,
                // pin: true,
            }
        })
        return()=>{
            pin.kill();
        }
    }, []);

    const sectionRef_2 =useRef(null);
    useEffect(()=>{
        const el = sectionRef_2.current;
        gsap.fromTo(el, {y:"550px", rotate: "-2deg",}, {rotate: "2deg",y: "0", duration:3, scrollTrigger: {
            trigger: el,
            start: "top 90%",
            end: "+=800",
            scrub: 1,
        }});
    });
    const sectionRef_3 =useRef(null);

    useEffect(()=>{
        const el = sectionRef_3.current;
        gsap.fromTo(el, {y:"850px"}, {y: "0", duration:3, scrollTrigger: {
            trigger: el,
            start: "top 90%",
            end: "+=800",
            scrub: 1,
        }});
    });
    return(
        <>
        <div className="portfolio_bg" > 
            <img ref={imgRef} className="portfolio_bg_img" src={portfolio_bg} alt="" />
            <div className="portfolio_bg_wrapper" >
                <h2 ref={portfolioText} className="font-BeatriceBold " >Gallery</h2>
                <div className="portfolio_bg_grid" ref={triggerRef_1}>
                    <div className="portfolio_bg_grid__container">
                        <img src={portfolio_item_1} alt=""/>
                    </div>
                    <div className="portfolio_bg_grid_2" ref={sectionRef_2}><img src={portfolio_item_1} alt="" /></div>
                    <div className="portfolio_bg_grid_3" ref={sectionRef_1}><img src={portfolio_item_3} alt="" /></div>
                    <div className="portfolio_bg_grid_4" ref={sectionRef_3}><img src={portfolio_item_1} alt="" /></div>
                </div>
                <button className="portfolio_bg_button"><Link to="/picture"><h6>  </h6><img src={buttonImg} alt="" /></Link></button>
                
            </div>
            
        </div>
        </>
    )
}

export default Portfolio;